import {useMemo} from 'react';

export const usePagination = (totalPages) => {
   function getResult(totalPages) {
      let result = [];
      for (let i = 0; i < totalPages; i++) {
         result.push(i + 1);
      }
      return result;
   }

   const pagination = useMemo(() => getResult(totalPages), [totalPages]);

   return pagination;
};
